<template>
  <div class="forum-home">
      <!-- 导航菜单 -->
      <el-menu :default-active="activeMenu" mode="horizontal" class="nav-menu" @select="handleMenuSelect">
        <el-menu-item style="cursor:default; padding-right:50px;font-size: 18px;opacity: 1;" disabled>海洋生物论坛</el-menu-item>  
        <el-menu-item index="index">首页</el-menu-item>
        <el-menu-item index="ocean">海洋区域</el-menu-item>
        <el-menu-item index="marine">海洋生物</el-menu-item>
        <el-menu-item index="info">海洋信息</el-menu-item>
        <el-menu-item index="news">海洋新闻</el-menu-item>
		<el-menu-item index="papers">我要发帖</el-menu-item>
		<el-menu-item v-if="role==='2'" index="researcher">科研预测</el-menu-item>
		<el-menu-item v-if="role==='1'" index="admin">管理员管理</el-menu-item>
	    
		<div v-if="id==null" style="float: right;margin-top:13px; display: block;margin-left: 40px;">
			<el-button type="primary" @click="goToRegister" style="margin-right: 15px;">注册</el-button>
			<el-button type="primary" @click="goToLogin">登录</el-button>
		</div>
		
		
		<!-- <p v-if="role==='2'" style="margin-left:20rem;margin-top:20px;">科学家</p>
		<p v-if="role==='1'" style="margin-top:20px;">管理员</p> -->
		<div v-if="id!=null" style="float: right;display: block; margin-top: 13px;margin-left: 40px;">
			<span style="cursor: pointer;margin-right: 15px;" @click="userinfo"><u>{{username}}</u></span>
			<el-button type="primary" @click="goToLogout">退 出 </el-button>
		</div>
		
      </el-menu>
    
    <!-- 主体内容 -->
    <el-main class="main-content">
      <!-- 左侧内容区域 -->
		<component :is="currentComponent"></component>

    </el-main>
    
    <!-- 底部说明 -->
    <!-- <el-footer class="footer">
      <p>海洋生物数据管理论坛</p>
    </el-footer> -->
  </div>
  
  
  <!-- 注册弹框 -->
  <el-dialog
      :visible.sync="dialogVisible"
	  v-model="dialogVisible"
      title=" 注  册 " 
      width="25%"
      center
      :close-on-click-modal="false"
	  style="border-radius: 10px;"
    >
      <el-form
        :model="form"
        :rules="rules"
        ref="registerForm"
        label-width="100px"
      >
	  
		<div class="avatar-upload" @click="handleUploadClick">
		  <input type="file" ref="fileInput" accept="image/*" style="display: none" @change="handleFileChange">
		  <img v-if='form.imageUrl' :src="form.imageUrl" class="avatar">
		  <img v-else :src="imageUrl" class="avatar">
		  <i class="el-icon-plus avatar-uploader-icon"></i>
		</div>
	  
        <el-form-item label="账号" prop="account">
          <el-input v-model="form.account"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input type="password" v-model="form.password"></el-input>
        </el-form-item>
        <el-form-item label="确认密码" prop="confirmPassword">
          <el-input type="password" v-model="form.confirmPassword"></el-input>
        </el-form-item>
        <el-form-item label="姓名" prop="name">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="手机号" prop="phone">
          <el-input v-model="form.phone"></el-input>
        </el-form-item>
      </el-form>
	  
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取消</el-button>
        <el-button type="primary" @click="register">注册</el-button>
      </div>
    </el-dialog>
  
  
	<!-- 登录弹框 -->
	<el-dialog
	    :visible.sync="dialogVisibleLogin"
		  v-model="dialogVisibleLogin"
	    title=" 登  录 "
	    width="25%"
	    center
	    :close-on-click-modal="false"
		style="border-radius: 10px;"
	  >
	    <el-form
	      :model="formLogin"
	      :rules="rulesLogin"
	      ref="loginForm"
	      label-width="100px"
	    >
	      <el-form-item label="账号" prop="accountLogin">
	        <el-input v-model="formLogin.accountLogin"></el-input>
	      </el-form-item>
	      <el-form-item label="密码" prop="passwordLogin">
	        <el-input type="password" v-model="formLogin.passwordLogin"></el-input>
	      </el-form-item>
	    </el-form>
		  
	    <div slot="footer" class="dialog-footer">
	      <el-button @click="dialogVisibleLogin = false">取消</el-button>
	      <el-button type="primary" @click="login">登录</el-button>
	    </div>
	  </el-dialog>
	  
	  
	  
	  <!-- 个人信息弹框 -->
	  <el-dialog
	      :visible.sync="infoDialogVisible"
	  	  v-model="infoDialogVisible"
	      title=" 个 人 信 息 " 
	      width="25%"
	      center
	      :close-on-click-modal="false"
		  style="border-radius: 10px;"
	    >
	      <el-form
	        :model="formInfo"
	        :rules="rules1"
	        ref="infoForm"
	        label-width="100px"
	      >
	  	  
	  		<div class="avatar-upload" @click="handleUploadClick">
	  		  <input type="file" ref="fileInput" accept="image/*" style="display: none" @change="handleFileChange">
	  		  <img v-if='formInfo.imageUrl' :src="formInfo.imageUrl" class="avatar">
			  <img v-else :src="imageUrl" class="avatar">
	  		  <i class="el-icon-plus avatar-uploader-icon"></i>
	  		</div>
	  	  
	        <el-form-item label="账号" prop="account">
	          <el-input v-model="formInfo.account" disabled></el-input>
	        </el-form-item>
	        <el-form-item label="原密码" prop="password">
	          <el-input type="password" v-model="formInfo.password"></el-input>
	        </el-form-item>
	        <el-form-item label="新密码" prop="confirmPassword">
	          <el-input type="password" v-model="formInfo.confirmPassword"></el-input>
	        </el-form-item>
			<el-form-item label="确认新密码" prop="confirmPassword1">
			  <el-input type="password" v-model="formInfo.confirmPassword1"></el-input>
			</el-form-item>
	        <el-form-item label="姓名" prop="name">
	          <el-input v-model="formInfo.name"></el-input>
	        </el-form-item>
	        <el-form-item label="手机号" prop="phone">
	          <el-input v-model="formInfo.phone"></el-input>
	        </el-form-item>
	      </el-form>
	  	  
	      <div slot="footer" class="dialog-footer">
	        <el-button @click="infoDialogVisible = false">取消</el-button>
	        <el-button type="primary" @click="updateInfo">修改</el-button>
	      </div>
	    </el-dialog>
	  
  
</template>

<script>
import { ref } from 'vue';
import axios from '@/api/index.js'
import { ElMessageBox, ElNotification } from 'element-plus'

import index from '@/views/general/index.vue'
import admin from '@/views/admin/index.vue'
import papers from '@/views/papers.vue'
import info from '@/views/info/info.vue'
import news from '@/views/news/news.vue'
import ocean from '@/views/ocean/oindex.vue'
import marine from '@/views/marine-line/mindex.vue'
import researcher from '@/views/researcher.vue'
import imgs from '@/assets/150.png'
import {baseUrl} from '@/api/index.js'

export default {
  name: 'HomeView',
  components:{
	  index,
	  admin,
	  papers,
	  info,
	  news,
	  ocean,
	  marine,
	  researcher,
  },
  data() {
    return {
	  imageUrl:imgs,
	  id:localStorage.getItem('id'),
	  username:localStorage.getItem('username'),
	  role:localStorage.getItem('role'),
      //loggedIn: username?true:false, // 用户是否已登录
      activeMenu: 'index',// 当前选中的菜单项
	  
	  currentComponent:'index',
	  
      dialogVisible: false,
	  form: {
		imageUrl: '', // 上传的头像图片链接
		account: '',
		password: '',
		confirmPassword: '',
		name: '',
		phone: '',
		img:'',
	  },
	  formInfo: {
		id:localStorage.getItem('id'),
		imageUrl: '', // 上传的头像图片链接
		account: '',
		password: '',
		confirmPassword: '',
		confirmPassword1:'',
		name: '',
		phone: '',
		img:'',
	  },
	  dialogVisibleLogin:false,
	  formLogin: {
	  		accountLogin: '',
	  		passwordLogin: '',
	  },
	  infoDialogVisible:false,
	  rules: {
		account: [{ required: true, message: '请输入账号', trigger: 'blur' }],
		password: [{ required: true, message: '请输入密码', trigger: 'blur' }],
		confirmPassword: [
		  { required: true, message: '请再次输入密码', trigger: 'blur' },
		  {
			validator: this.checkPasswordConfirmation,
			trigger: 'blur'
		  }
		],
		name: [{ required: true, message: '请输入姓名', trigger: 'blur' }],
		phone: [{ required: true, message: '请输入手机号', trigger: 'blur' }]
	  },
	  rulesLogin: {
	  		accountLogin: [{ required: true, message: '请输入账号', trigger: 'blur' }],
	  		passwordLogin: [{ required: true, message: '请输入密码', trigger: 'blur' }],
	  },
	  rules1: {
	  		account: [{ required: true, message: '请输入账号', trigger: 'blur' }],
	  		name: [{ required: true, message: '请输入姓名', trigger: 'blur' }],
	  		phone: [{ required: true, message: '请输入手机号', trigger: 'blur' }]
	  },
	};
  },
  created() {
	  //console.log('role=',typeof(this.role));
	  //console.log('id=',this.id);
    //this.fetchData();
  },
  methods: {
	  updateInfo(){
		  this.$refs.infoForm.validate(valid => {
		  		if (valid) {
		  			if(this.formInfo.account.length<6){
		  				ElNotification({
		  				  title: '系统提示',
		  				  message: '用户名需大于6位~',
		  				  duration: 3000,
		  				});
		  				return;
		  			}
		  			if(this.formInfo.password.length>0){
						if(this.formInfo.confirmPassword.length<5||this.formInfo.confirmPassword1.length<5){
							ElNotification({
							  title: '系统提示',
							  message: '新密码或确认新密码需大于等于6位',
							  duration: 3000,
							});
							return;
						}
						if(this.formInfo.confirmPassword!=this.formInfo.confirmPassword1){
							ElNotification({
							  title: '系统提示',
							  message: '新密码和确认新密码应该相同',
							  duration: 3000,
							});
							return;
						}	
		  			}
		  			if(!/^[1][3,4,5,6,7,8,9][0-9]{9}$/.test(this.formInfo.phone)){
		  				ElNotification({
		  				  title: '系统提示',
		  				  message: '手机格式错误~',
		  				  duration: 3000,
		  				});
		  				return;
		  			}
		  					
		  		  this.formInfo.img = this.formInfo.imageUrl.replace(baseUrl,'');
		  			
		  		  // 执行修改逻辑
		  		  axios.post('/api/update_info',this.formInfo,{headers:{"Access-Control-Allow-Origin":"*"}})
		  		  .then(response => {
		  		  		  //console.log(response);
		  				if(response.data.code==0){
		  					ElNotification({
		  					  title: '系统提示',
		  					  message: '修改成功~',
		  					  duration: 3000,
		  					});
		  					this.infoDialogVisible=false;
		  				}else{
		  					ElNotification({
		  					  title: '系统提示',
		  					  message: response.data.msg,
		  					  duration: 3000,
		  					})
		  				}
		  		  		// 请求成功，将数据绑定到表格中
		  		  		//this.tableData = response.data.data;
		  		  		//this.total=this.tableData.length;
		  		  		//this.handleSearch();
		  		  })
		  		  .catch(error => {
		  		  		// 请求失败，处理错误
		  				ElNotification({
		  				  title: '系统提示',
		  				  message: '登录失败',
		  				  duration: 3000,
		  				})
		  		  		console.error('请求数据失败:', error);
		  		  });
		  		  //console.log('注册成功');
		  		  //this.dialogVisible = false;
		  		} else {
		  		  console.log('表单验证失败');
		  		  return false;
		  		}
		  });
	  },
	  userinfo(){
		  console.log(this.form)
		  this.infoDialogVisible=true;
	  },
	  handleUploadClick() {
	    this.$refs.fileInput.click();
	  },
	  async handleFileChange(event) {
	    const file = event.target.files[0];
	    console.log('file='+file);
	    if(!file){
	  	  ElNotification({
	  	    title: '系统提示',
	  	    message: '请选择需要上传的文件',
	  	    //message:error,
	  	    duration: 3000,
	  	  })
	  	  return;
	    }
	    const formDatas = new FormData();
	    formDatas.append('file', file);
	  
	    const response = await axios.post('/api/info_upload', formDatas, {
	  	headers: {
	  	  'Content-Type': 'multipart/form-data'
	  	}
	    });
	    //console.log('1服务器返回的数据:', response);
	    if(response.data.code==0){
	  	  //this.imageUrl = baseUrl + response.data.data;
		  this.form.imageUrl = baseUrl + response.data.data;
		  this.formInfo.imageUrl = baseUrl + response.data.data;
		  console.log('55=', this.imageUrl)
		  console.log('66=', this.form.imageUrl)
	    }else{
	  	  ElNotification({
	  	    title: '系统提示',
	  	    message: '上传失败',
	  	    //message:error,
	  	    duration: 3000,
	  	  })
	    }
	    //alert('文件上传成功');
	    //console.log('2服务器返回的数据:', response.data.data);
	    
	    
	    // if (file) {
	    //   this.imageUrl = URL.createObjectURL(file);
	    // }
	  },
	  
	  
	  goToLogin(){
		  this.formLogin = this.$options.data().formLogin;
		  // this.formLogin.accoutLogin='';
		  // this.formLogin.passwordLogin='';
		  //console.log('背电极了222');
		  this.dialogVisibleLogin=true;
	  },
	  goToRegister(){
		  this.form = this.$options.data().form;
		  // form: {
		  // 		imageUrl: '', // 上传的头像图片链接
		  // 		account: '',
		  // 		password: '',
		  // 		confirmPassword: '',
		  // 		name: '',
		  // 		phone: '',
		  // 		img:'',
		  // },
		  //console.log('背电极了');
		  this.dialogVisible=true;
	  },
	  goToLogout(){
		  localStorage.removeItem('username');
		  localStorage.removeItem('role');
		  localStorage.removeItem('id');
		  this.id=localStorage.getItem('id');
		  this.username=localStorage.getItem('username');
		  this.role=localStorage.getItem('role');
		  this.currentComponent='index';
		  this.activeMenu = 'index';
	  },
    checkPasswordConfirmation(rule, value, callback) {
		if (value !== this.form.password) {
		  callback(new Error('两次输入的密码不一致'));
		} else {
		  callback();
		}
	},
	register() {
	  this.$refs.registerForm.validate(valid => {
		if (valid) {
			if(this.form.account.length<6){
				ElNotification({
				  title: '系统提示',
				  message: '用户名需大于6位~',
				  duration: 3000,
				});
				return;
			}
			if(this.form.password.length<6){
				ElNotification({
				  title: '系统提示',
				  message: '密码需大于6位~',
				  duration: 3000,
				});
				return;
			}
			if(this.form.password!=this.form.confirmPassword){
				ElNotification({
				  title: '系统提示',
				  message: '两次输入的密码不一致~',
				  duration: 3000,
				});
				return;
			}
			if(!/^[1][3,4,5,6,7,8,9][0-9]{9}$/.test(this.form.phone)){
				ElNotification({
				  title: '系统提示',
				  message: '手机格式错误~',
				  duration: 3000,
				});
				return;
			}

		  this.form.img = this.form.imageUrl.replace(baseUrl,'');

		  // 执行注册逻辑
		  axios.post('/api/register',this.form,{headers:{"Access-Control-Allow-Origin":"*"}})
		  .then(response => {
		  		  //console.log(response);
				if(response.data.code==0){
					ElNotification({
					  title: '系统提示',
					  message: '注册成功~',
					  duration: 3000,
					});
					this.dialogVisible=false;
				}else{
					ElNotification({
					  title: '系统提示',
					  message: response.data.msg,
					  duration: 3000,
					})
				}
		  		// 请求成功，将数据绑定到表格中
		  		//this.tableData = response.data.data;
		  		//this.total=this.tableData.length;
		  		//this.handleSearch();
		  })
		  .catch(error => {
		  		// 请求失败，处理错误
				ElNotification({
				  title: '系统提示',
				  message: '登录失败',
				  duration: 3000,
				})
		  		console.error('请求数据失败:', error);
		  });
		  //console.log('注册成功');
		  //this.dialogVisible = false;
		} else {
		  console.log('表单验证失败');
		  return false;
		}
	  });
	},
	login(){
		//this.formLogin.accountLogin=''
		this.$refs.loginForm.validate(valid => {
			if (valid) {
				if(this.formLogin.accountLogin.length<6){
					ElNotification({
					  title: '系统提示',
					  message: '用户名需大于6位~',
					  duration: 3000,
					});
					return;
				}
				if(this.formLogin.passwordLogin.length<6){
					ElNotification({
					  title: '系统提示',
					  message: '密码需大于6位~',
					  duration: 3000,
					});
					return;
				}
			  // 执行登录逻辑
			  axios.post('/api/login',this.formLogin,{headers:{"Access-Control-Allow-Origin":"*"}})
			  .then(response => {
			  		//console.log(response);
			  		if(response.data.code==0){
			  			//this.loggedIn=true;
						this.username=response.data.data.username;
						this.role=String(response.data.data.role);
						this.id = response.data.data.id;
						localStorage.setItem('username',response.data.data.username);
						localStorage.setItem('role',String(response.data.data.role));
						localStorage.setItem('id',response.data.data.id);
						this.dialogVisibleLogin=false;
						
						this.formInfo.account = response.data.data.username;
						this.formInfo.name = response.data.data.full_name;
						this.formInfo.phone = response.data.data.phone_number;
						this.formInfo.imageUrl = baseUrl+response.data.data.imageUrl;
						
						//console.log('222=', this.form)
						
						//console.log('role=',this.role);
						//console.log('id=',this.id);
						this.currentComponent='index';
						this.activeMenu = 'index';
			  		}else{
			  			ElNotification({
			  			  title: '系统提示',
			  			  message: response.data.msg,
			  			  duration: 3000,
			  			})
			  		}
			  })
			  .catch(error => {
			  		// 请求失败，处理错误
					ElNotification({
					  title: '系统提示',
					  message: '登录失败',
					  duration: 3000,
					})
			  		console.error('请求数据失败:', error);
			  });
			  
			  //console.log('注册成功');
			  //this.dialogVisible = false;
			} else {
			  console.log('表单验证失败');
			  return false;
			}
		});
	},
	
    handleMenuSelect(index) {
      this.activeMenu = index;
	  if('papers'==index){
		  if(localStorage.getItem('id')==null||localStorage.getItem('id')==''){
			  ElNotification({
			    title: '发帖提示',
			    message: '请登录后再发帖',
			    duration: 3000,
			  });
			  //this.activeMenu = '';
			  return;
		  }
	  }
	  this.currentComponent = index;
      // 根据选中的菜单项执行相应的操作，比如切换内容
    }
  }
};
</script>

<style scoped>
