.forum-home {
  display: flex;
  flex-direction: column;
  height: 98vh;
}

.top-nav {
  /* background-color: #62a9ff; 导航背景色 */
  background-color: #A9A9A9;

}

.top-nav-inner {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 0 250px; /* 左右页边距 */
}

.guest-info {
  margin-right: 20px;
}

.nav-menu {
  margin: 0; /* 去掉默认边距 */
  padding: 0 250px; /* 左右页边距 */
  /* background-color: #eebe77; */
  background-color: #20B2AA;
}

.main-content {
  /* display: flex; */
  flex: 1;
  padding: 0 250px;
}

.avatar-upload {
  text-align: center;
  margin-bottom: 20px;
}

.avatar-uploader-icon {
  font-size: 28px;
  color: #999;
  cursor: pointer;
  display: block;
  margin-top: 10px;
}

.avatar {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  cursor: pointer;
}
/* .right-sidebar {
  background-color: #f0f0f0;
  padding: 20px;
  margin-top:50px;
}
 */

.forum-posts {
  margin-bottom: 20px;
}

.footer {
  text-align: center;
  padding: 20px;
}
.dialog-footer{
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>
