<template>
	<!-- 左侧帖子部分 -->
	  <div class="left-content" v-if="lists">
		<div class="post-list" style="min-height: 500px;">
			<!-- 查询输入框 -->
			<div class="input-with-button" style="margin-bottom: 20px;">
				<el-input v-model="keyword" placeholder="请输入查询关键字" class="input" style="width:500px;"></el-input>
				<el-button type="primary" @click="handleForSearch"> 查  询 </el-button>
			</div>
			
			<div class="forum-table" style="min-height: 300px;margin-bottom: 30px;">
				<el-table :data="filteredData" style="width: 100%;" :show-header="false">
				  <el-table-column prop="title" min-width="75%">
					<template #default="{ row }">
					  <div @click="handlePostTitleClick(row)" style="cursor: pointer;">{{ row.title }}</div>
					</template>
				  </el-table-column>
				  
				  <el-table-column prop="time" min-width="10%" style="color: darkgrey;">
					<template #default="{ row }">
						<span v-if="row.role==1">管理员</span>
						<span v-if="row.role==2">科学家</span>
						<span v-if="row.role==3">用户</span>
					</template>
				  </el-table-column>
				  
				  
				  <el-table-column prop="time" min-width="15%" style="color: darkgrey;">
					<template #default="{ row }">{{ row.time }}</template>
				  </el-table-column>
				  <el-table-column prop="username" min-width="10%" style="color: darkgrey;">
					<template #default="{ row }">{{ row.username }}</template>
				  </el-table-column>
				</el-table>
			</div>
			
		</div>
			
		<!-- 分页器 -->
		<div class="pagination">
		  <el-pagination
			v-model:currentPage="currentPage"
			:page-size="pageSize"
			:total="posts.length"
			layout="prev, pager, next"
			@current-change="handlePageChange"
		  />
		
		</div>
	  </div>
	  
	<!--  ------------------------------------------------------------------------------------------- -->  
	<!-- 帖子内容 -->
	<div class="forum-detail" v-if="content"  style="width: 100%;margin-top:30px;">
	  <!-- 帖子标题 -->
	  <h1 class="post-title">{{ title }}</h1>
	  
	   <el-button type="primary" @click="backto" style="margin-top: 5px;">返回帖子列表</el-button>
	   <div class="forum-table" style="min-height: 300px;margin-top:5px;">
		   <el-table :data="paperData" border style="width: 100%;" :show-header="false" >
				<el-table-column prop="paperUsername"  min-width="15%">
					<template #default="{ row }">
					  <div style="text-align: center;min-height: 100px;">
						  <div v-if='row.imageUrl' style="text-align: center;"><img :src="baseUrl+row.imageUrl" class="avatar"/></div>
						  <div v-else style="text-align: center;">
							  <div v-if="row.role==1"><img :src="researcherpng" class="avatar"/></div>
							  <div v-if="row.role==2"><img :src="researcherpng" class="avatar"/></div>
							  <div v-if="row.role==3"><img :src="userpng" class="avatar"/></div>
						  </div>
						  <!-- <div v-if="row.role==1"><img :src="adminpng" class="avatar"/></div>
						  <div v-if="row.role==2"><img :src="researcherpng" class="avatar"/></div>
						  <div v-if="row.role==3"><img :src="userpng" class="avatar"/></div> -->
						  <div>{{ row.paperUsername }}</div>
						  <div v-if="row.role==1" style="color: red;">管理员</div>
						  <div v-if="row.role==2" style="color: blue;">科学家</div>
					  </div>
					</template>
				</el-table-column>
				<el-table-column  prop="paperContent"  min-width="85%">
					<template #default="{ row }">
					  <div style="min-height: 100px;">
						  <div v-html="row.paperContent" style="min-height: 170px;"></div>
						  <div style="width:96%;">
							  <div style="text-align: left;color: darkgray; display: inline-block; width:50%;padding-left:20px;">评分:{{ currentScore }}</div>
							  <div style="text-align: right;color: darkgray; display: inline-block; width:50%;">发表于:{{ row.time }}</div>
						  </div>
						  <!-- <div v-html="row.paperContent"></div>
						  <div style="position: absolute;bottom: 0;width:96%;text-align: right;color: darkgray;">发表于:{{ row.time }}</div> -->
					  </div>
					</template>
				</el-table-column>
		   </el-table>
		  <el-table :data="tableData" border style="width: 100%;" :show-header="false" >
			<el-table-column prop="username" min-width="15%">
				<template #default="{ row }">
				  <div style="text-align: center;min-height: 100px;">
					  <div v-if='row.imageUrl'><img :src="baseUrl+row.imageUrl" class="avatar"/></div>
					  <div v-else>
						  <div v-if="row.role==1"><img :src="researcherpng" class="avatar"/></div>
						  <div v-if="row.role==2"><img :src="researcherpng" class="avatar"/></div>
						  <div v-if="row.role==3"><img :src="userpng" class="avatar"/></div>
					  </div>
					  <!-- <div v-if="row.role==1"><img :src="adminpng" class="avatar"/></div>
					  <div v-if="row.role==2"><img :src="researcherpng" class="avatar"/></div>
					  <div v-if="row.role==3"><img :src="userpng" class="avatar"/></div> -->
					  <div>{{ row.username }}</div>
					  <div v-if="row.role==1" style="color: red;">管理员</div>
					  <div v-if="row.role==2" style="color: blue;">科学家</div>
				  </div>
				</template>
			</el-table-column>
			<el-table-column prop="content" min-width="85%">
				<template #default="{ row }">
				 <div style="min-height: 200px;">
				  <div style="min-height: 170px;">{{ row.content }}</div>
				  
				   <el-table v-if="row.newComments.length!=0" :data="row.newComments" border style="width: 100%;" :show-header="false" empty-text=' '>
					<el-table-column min-width="100%">
						<template #default="{ row }">
						  <div style="">
							  
							  <div>{{row.usernamen}} 的评论：</div>
							  <div style="margin-left:30px;">{{ row.content }}</div>
							  
						  </div>
						  <div style="text-align: right;color: darkgray; display: inline-block;width:98%">发表于:{{ row.ctime }}</div>
						</template>
					</el-table-column>
				  </el-table>
				  
				  
				  <div style="width:96%;">
					  <div style="text-align: left;color: darkgray;display: inline-block;width:25%;" @click="pinglun(row)">
					  						  <u style="cursor: pointer;"><el-icon :size="14" style="padding-top: 10px;padding-right: 4px;color:lightblue; "><ChatRound /></el-icon> 评 论</u>
					   </div>
					  
					  <div v-if="userid" style="text-align: left;color: darkgray;display: inline-block;width:25%;">
					  						  <!-- <i class="el-icon-star-off"></i>
					  						  <i class="el-icon-star-on"></i> -->
					  						  <el-icon v-if="row.liked==1" :size='18' style="color:lightblue;cursor: pointer; " title="取消点赞"  @click="clickLikeCancel(row)">
					  							  <StarFilled />
					  						  </el-icon>
					  						  <el-icon  v-if="row.liked==0" :size='18' style="color:lightblue;cursor: pointer; " title="点赞" @click="clickLike(row)">
					  						  	  <Star />
					  						  </el-icon>
					  </div>
					  
					  <div v-else style="text-align: left;color: darkgray; display: inline-block;width:25%;">
					  						  <!-- <i class="el-icon-star-off"></i> -->
					  						  <el-icon  :size='24' style="color:lightblue;cursor: pointer; " title="点赞" @click="clickLike(row)">
					  						  	<Star />
					  						  </el-icon>
					  </div>
					  
					  <div v-if="row.likes == null" style="text-align: center;color: darkgray; display: inline-block;width:25%;">
					  						  <el-icon :size='18' style="color:lightblue;vertical-align:top;" title="点赞数">
					  						  	<StarFilled />
					  						  </el-icon><span style="">点赞数:0</span>
					  </div>
					  <div v-if="row.likes != null" style="text-align: center;color: darkgray; display: inline-block;width:25%;">
					  						  <el-icon :size='18' style="color:lightblue;vertical-align:top;" title="点赞数">
					  						  	<StarFilled />
					  						  </el-icon><span style="">点赞数:{{row.likes}}</span>
					  </div>
					  
					  <div style="text-align: right;color: darkgray; display: inline-block;width:25%;">发表于:{{ row.comment_time }}</div>
					  
					 <!-- <div style="text-align: left;color: darkgray;cursor: pointer; display: inline-block;width:25%;" @click="pinglun(row)"><u>点 击 评 论</u></div>
					  <div style="text-align: left;color: darkgray;cursor: pointer; display: inline-block;width:25%;" @click="clickLike(row)"><u>点 赞</u></div>
					  <div v-if="row.likes == null" style="text-align: center;color: darkgray; display: inline-block;width:25%;">点赞数:0</div>
					  <div v-if="row.likes != null" style="text-align: center;color: darkgray; display: inline-block;width:25%;">点赞数:{{ row.likes }}</div>
					  <div style="text-align: right;color: darkgray; display: inline-block;width:25%;">发表于:{{ row.comment_time }}</div> -->
				  </div>
				 </div>
				</template>
			</el-table-column>
		  </el-table>
		</div>
		<el-button type="primary" @click="backto" style="margin-top: 10px;margin-bottom: 30px;">返回帖子列表</el-button>
	
	  <!-- 分页 -->
	  <el-pagination
	    v-model:currentPage="currentPage1"
	    :page-size="pageSize1"
	    :total="totalComments1"
	    layout="prev, pager, next"
	    @current-change="handlePageChange1"
	  />
	
	  <!-- 发表评论 -->
	  <div class="comment-submit" style="margin-top: 20px;margin-bottom: 10px;">发表评论：</div>
	  <el-input
	    v-model="newComment"
	    type="textarea"
	    :rows="5"
	    placeholder="请输入评论内容"
	    class="comment-input"
	  ></el-input>
	  <h5>打分：</h5>
	  <el-rate v-model="score" :max="5" show-text allow-half @change="handleScoreChange"></el-rate>
	  <p>您的评分是: {{ score }}</p>
	  <el-button type="primary" @click="submitComment" style="margin-top: 10px;">发   表</el-button>
	</div>  
	
	<el-dialog
	    :visible.sync="dialogVisible"
		  v-model="dialogVisible"
	    title=" 评 论 "
	    width="45%"
	    center
	    :close-on-click-modal="false"
	  >
	    <el-input
	      v-model="sendNewComment"
	      type="textarea"
	      :rows="3"
	      placeholder="请输入评论内容"
	      class="comment-input"
	    ></el-input>
		  
	    <div slot="footer" class="dialog-footer" style="text-align: center;margin-top: 10px;">
	      <el-button @click="dialogVisible = false">取消</el-button>
	      <el-button type="primary" @click="sendComment"> 发 表 </el-button>
	    </div>
	  </el-dialog>
</template>

<script>
import axios from '@/api/index.js'
import { ElMessageBox, ElNotification } from 'element-plus'
import adminpng from '@/assets/admin.jpg'
import researcherpng from '@/assets/researcher.jpg'
import userpng from '@/assets/user.png'
import {baseUrl} from '@/api/index.js'

export default {
  comments:{
	  // adminpng,
	  // researcherpng,
	  // userpng,
  },
  data() {
    return {
		baseUrl:baseUrl,
	  //图片
      adminpng:adminpng,
	  researcherpng:researcherpng,
	  userpng:userpng,
	  
      currentPage: 1,
      keyword: '',
	  posts: [],
	  filteredData:[],
	  pageSize:15,
	  
	  //控制列表和内容显示隐藏
	  lists:true,
	  content:false,
	  
	  //以下为content字段
	  paperData:[],  //此为第一行数据准备
	  // paperUsername:'',
	  // paperContent:'',
	  title:'我是一个标题',
	  comments:[], //分页前原始数据
	  newComment:'',
	  
	  totalComments1:0,
	  pageSize1:10,
	  currentPage1: 1,
	  
	  tableData: [], //分页数据
	  
	  userid:localStorage.getItem('id'), //用户ID，判断是否登录
	  paperid:0,
	  
	  currentPost:'',
	  dialogVisible:false,
	  
	  commentId:0, //正在评论的评论id
	  sendNewComment:'',
	  score: 0,
	  currentScore:0,
    };
  },
  computed: {

  },
  created(){
	this.fetchData();
  	//console.log(22222);
  },
  methods: {
	  clickLike(row){
		  if(this.userid==''||this.userid==null){
		  	ElNotification({
		  	  title: '系统提示',
		  	  message: '请先登录后再点赞',
		  	  duration: 3000,
		  	})
		  	return;
		  }else{
			axios.post('/api/clickLike',{'comment_id':row.id,'section':'news','user_id':this.userid},{headers:{"Access-Control-Allow-Origin":"*"}})
			.then(response => {
				console.log('2222=',this.currentPost);
				ElNotification({
				  title: '系统提示',
				  message: '点赞成功',
				  duration: 3000,
				})
				
				this.handlePostTitleClick(this.currentPost);
			})
			.catch(error => {
				// 请求失败，处理错误
				ElNotification({
				  title: '系统提示',
				  message: '点赞失败',
				  duration: 3000,
				})
				console.error('请求数据失败:', error);
			}); 
		}
	  },
	  clickLikeCancel(row){
	  	if(this.userid==''||this.userid==null){
	  		ElNotification({
	  		  title: '系统提示',
	  		  message: '请先登录后再点赞',
	  		  duration: 3000,
	  		})
	  		return;
	  	}else{
	  		axios.post('/api/clickLikeCancel',{'comment_id':row.id,'section':'news','user_id':this.userid},{headers:{"Access-Control-Allow-Origin":"*"}})
	  		.then(response => {
	  			//console.log('2222=',this.currentPost);
	  			ElNotification({
	  			  title: '系统提示',
	  			  message: '取消成功',
	  			  duration: 3000,
	  			})
	  			
	  			this.handlePostTitleClick(this.currentPost);
	  		})
	  		.catch(error => {
	  			// 请求失败，处理错误
	  			ElNotification({
	  			  title: '系统提示',
	  			  message: '取消失败',
	  			  duration: 3000,
	  			})
	  			console.error('请求数据失败:', error);
	  		}); 
	  	}
	  },
	  sendComment(){
	  	if(this.userid==''||this.userid==null){
	  		ElNotification({
	  		  title: '系统提示',
	  		  message: '请先登录后再评论',
	  		  duration: 3000,
	  		})
	  		return;
	  	}else{
	  		if(this.sendNewComment==''){
	  			ElNotification({
	  			  title: '系统提示',
	  			  message: '评论不能为空',
	  			  duration: 3000,
	  			})
	  			return;
	  		}else{
	  			//此处paperid为当前评论ID
	  			axios.post('/api/add_comment',{'paper_id':this.commentId,'user_id':this.userid,'section':'news_new','newComment':this.sendNewComment},{headers:{"Access-Control-Allow-Origin":"*"}})
	  			.then(response => {
	  				console.log('2222=',this.currentPost);
	  				// ElNotification({
	  				//   title: '系统提示',
	  				//   message: response.data.msg,
	  				//   duration: 3000,
	  				// })
	  				this.dialogVisible=false;
	  				this.sendNewComment='';
	  				
	  				this.handlePostTitleClick(this.currentPost);
	  			})
	  			.catch(error => {
	  				// 请求失败，处理错误
	  				ElNotification({
	  				  title: '系统提示',
	  				  message: '评论失败',
	  				  duration: 3000,
	  				})
	  				console.error('请求数据失败:', error);
	  			}); 
	  		}
	  	}
	  },
	  pinglun(row){
	  	this.dialogVisible=true;
	  	this.commentId=row.id;
	  },
	//返回帖子列表
	backto(){
		this.lists=true;
		this.content=false;
	},
	 
	 //发表评论 
	submitComment(){
		//console.log(this.userid);
		if(this.userid==''||this.userid==null){
			ElNotification({
			  title: '系统提示',
			  message: '请先登录后再评论',
			  duration: 3000,
			})
			return;
		}else{
			if(this.newComment==''){
				ElNotification({
				  title: '系统提示',
				  message: '评论不能为空',
				  duration: 3000,
				})
				return;
			}else{
				axios.post('/api/add_comment',{'paper_id':this.paperid,'user_id':this.userid,'section':'news','newComment':this.newComment,'score':this.score},{headers:{"Access-Control-Allow-Origin":"*"}})
				.then(response => {
					console.log('2222=',this.currentPost);
					// ElNotification({
					//   title: '系统提示',
					//   message: response.data.msg,
					//   duration: 3000,
					// })
					this.newComment='';
					
					this.handlePostTitleClick(this.currentPost);
				})
				.catch(error => {
					// 请求失败，处理错误
					ElNotification({
					  title: '系统提示',
					  message: '评论失败',
					  duration: 3000,
					})
					console.error('请求数据失败:', error);
				});   
			}
		}
	},
	handlePageChange1(page) {
		//console.log('page=',page);
		//console.log('currentPage1=',this.currentPage1);
		this.currentPage1 = page;
		this.handleSearch1();
	},
    handlePageChange(page) {
      this.currentPage = page;
	  this.handleSearch();
    },
	handlePostTitleClick(post) {
	  //console.log('Post title clicked:', post);
	  this.currentPost = post;
	  // 这里可以添加点击标题后的逻辑处理
	  this.lists=false;
	  this.content=true;
	  this.paperid=post.id;
	  
	  this.title=post.title;
	  this.paperData=[{paperUsername:post.username,paperContent:post.content,role:post.role,time:post.time}];
	  
	  this.comments=[];
	  this.tableData=[];
	  this.totalComments1=0;
	  
	  axios.post('/api/paper_content',{'paper_id':post.id,'section':'news','user_id':this.userid},{headers:{"Access-Control-Allow-Origin":"*"}})
	  .then(response => {
	  	//console.log(response);
	  	if(response.data.code==0){
	  		this.comments = response.data.data;
			this.totalComments1 = response.data.data.length;
			if(this.totalComments1>0){
				this.currentScore=response.data.data[0]['sc'];
			}
	  		this.handleSearch1();
	  		//console.log('totalComments1=',this.totalComments1);
	  	}else{
	  		// ElNotification({
	  		//   title: '系统提示',
	  		//   message: response.data.msg,
	  		//   duration: 3000,
	  		// })
	  	}
	  })
	  .catch(error => {
	  	// 请求失败，处理错误
	  	ElNotification({
	  	  title: '系统提示',
	  	  message: '发帖失败',
	  	  duration: 3000,
	  	})
	  	console.error('请求数据失败:', error);
	  });   
	},

	fetchData(){
		axios.post('/api/paper_list',{'section':'news','keyword':this.keyword},{headers:{"Access-Control-Allow-Origin":"*"}})
		.then(response => {
			//console.log(response);
			if(response.data.code==0){
				this.posts = response.data.data;
				this.handleSearch();
				//console.log(response);
			}else{
				ElNotification({
				  title: '系统提示',
				  message: response.data.msg,
				  duration: 3000,
				})
			}
		})
		.catch(error => {
			// 请求失败，处理错误
			ElNotification({
			  title: '系统提示',
			  message: '发帖失败',
			  duration: 3000,
			})
			console.error('请求数据失败:', error);
		}); 
	},
	handleSearch() {
	  // 查询逻辑
	  //console.log('查询关键词:', this.keyword);
	  // 根据关键词过滤数据并进行分页
	
	  this.filteredData = this.posts.slice((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize);
		
	},
	handleSearch1() {
	  // 查询逻辑
	  //console.log('查询关键词:', this.keyword);
	  // 根据关键词过滤数据并进行分页
	
	  this.tableData = this.comments.slice((this.currentPage1 - 1) * this.pageSize1, this.currentPage1 * this.pageSize1);
	  //console.log('tableData==',this.tableData)
		
	},
	
	handleForSearch(){
		this.fetchData();
	},
	
  }
};
</script>

<style scoped>
.left-content {
  flex: 1;
  padding: 20px;
  margin-top:30px;
}
.post-list {
  margin-bottom: 10px;
}

.search-input {
  margin-bottom: 20px;
}

.post-item {
  display: flex;
  margin-bottom: 20px;
}

.post-content {
  flex: 1;
}

.post-title {
  font-size: 18px;
  font-weight: bold;
}

.post-meta {
  display: flex;
  justify-content: flex-end;
}

.post-meta span {
  margin-left: 10px;
}

.pagination {
  text-align: center;
  margin-top: 20px;
}

.input-with-button {
  display: flex;
  align-items: center;
}

.input {
  margin-right: 10px;
}
.avatar {
  width: 90px;
  height: 90px;
  border-radius: 50%; /* 圆形 */
  /* margin-right: 10px; */
}
</style>